import React from "react";
import * as Icons from "../../assets/icons";
import styled from "@emotion/styled";
const IconContainer = styled.div `
    height: 16px;
    width: 14px;
    cursor: pointer;
    ${(props) => props.sx};
`;
export const Icon = (props) => {
    const { name, sx, onClick } = props;
    const handleComponentClick = () => {
        onClick();
    };
    const SpecificIcon = Icons[name];
    return (React.createElement(IconContainer, { sx: sx, onClick: handleComponentClick }, SpecificIcon && React.createElement(SpecificIcon, null)));
};
